.wrapper {
  margin: 8px;
  background: grey;
}

.box {
  margin-bottom: 4px;
  position: relative;
  width: 60px;
  height: 60px;
  background: white;
  border: 2px solid brown;
}

.position1,
.position2,
.position3 {
  border-radius: 100%;
  background: burlywood;
}

.short {
  width: 264px;
}

.controls {
  width: 50%;
  margin-right: auto;
  margin-left: auto;
}

.svg{
    background: khaki;
    border:2px solid brown;
}

.ball{
    position: relative;
    border-radius: 100%;
    background: coral;           
}

